import React from 'react';
import moment from 'moment'; 
import {useDispatch} from 'react-redux';
import { deletePosts } from '../../../actions/posts';


const Post = ({post,setCurrentId}) => {

  const dispatch=useDispatch();
   return (
    <div className="card" style={{width:"100%",height:"100%"}}>
        
        <div className='card-top' style={{height:"200px",backgroundColor:'rgba(0,0,0,1)',zIndex:'0'}}>
        <h4 style={{color:'white',paddingLeft:"40px",paddingTop:"20px",zIndex:'1'}}>{post.creator}</h4>
          <p style={{color:'white',paddingLeft:"40px",lineHeight:"10px"}}>{moment(post.createdAt).fromNow()}</p>
          <div className='top-text'  style={{backgroundImage:`url(${post.selectedFile})`,width:'100%',height:'200px',top:'0px',left:'0px',position:'absolute',zIndex:'-1',backgroundSize:'cover',opacity:'0.8'}}>
           </div>
         
        </div>
        <div className="card-body">
        <p style={{color:"#495057",fontSize:"15px"}}>{post.tags.map((tag)=>`#${tag} `)}</p>
        
        <h5 className="card-title">{post.title}</h5>
        <p className="card-text" style={{color:"#495057",paddingTop:"10px",paddingBottom:"10px",fontSize:"15px"}}>{post.message}</p>
        
        <button onClick={()=>setCurrentId(post._id)}  className='fa fa-edit ' style={{textDecoration:"none",color:"#DC3545",float:"left",backgroundColor:"white",border:"none"}}> Edit</button>
        <button onClick={()=>dispatch(deletePosts(post._id))}  className='fa fa-trash-o ' style={{textDecoration:"none",color:"#DC3545",float:"right",backgroundColor:"white",border:"none"}}> Delete</button>
        </div>
  </div>
  );
};

export default Post;
