import React, { useState ,useEffect} from 'react';
import { useDispatch ,useSelector} from 'react-redux';
import { createPosts,updatePosts } from '../../actions/posts';
import FileBase64 from 'react-file-base64';

export const Form = ({currentId,setCurrentId}) => {

    const dispatch=useDispatch();
    const[postData,setPostData]=useState({
        creator:'',title:'', message:'', tags:'',selectedFile:'' });


     
    //Fetch all data in form field when user Update Data
    const post=useSelector((state)=>currentId ? state.posts.find((p)=>p._id === currentId):null);
    useEffect(()=>{
        if(post)
            setPostData(post);
           
         } ,[post]);


    

    const handleSubmit=(e)=>{
        e.preventDefault();
        //Check it is update or create action
        if(currentId){
            dispatch(updatePosts(currentId,postData));
            handleClear();
        }
        else{
            dispatch(createPosts(postData)); 
            handleClear();
        }
       
    }

   
    const handleClear=()=>{
        setCurrentId(null);
        setPostData({creator:'',title:'', message:'', tags:'',selectedFile:''});
    }

  return (
      <div className='container-fluid'>
          <div className='bg-light pt-3 pb-3 pl-2 pr-2'>
              <h6 className='text-center'>{currentId ?'Editing':'Creating'} a Memory</h6>
            <div className="m-3">
                <label htmlFor="exampleFormControlInput1" className="form-label">Creator</label>
                <input type="text" className="form-control" id="txt_creator"  value={postData.creator} onChange={(e)=>setPostData({...postData,creator:e.target.value})}/>
            </div>
            <div className="m-3">
                <label htmlFor="exampleFormControlInput1" className="form-label">Title</label>
                <input type="text" className="form-control" id="txt_title" value={postData.title} onChange={(e)=>setPostData({...postData,title:e.target.value})}/>
            </div>
            <div className="m-3">
                <label htmlFor="exampleFormControlTextarea1" className="form-label">Message </label>
                <textarea className="form-control" id="txt_message" rows="3" value={postData.message} onChange={(e)=>setPostData({...postData,message:e.target.value})}></textarea>
            </div>
            <div className="m-3">
                <label htmlFor="exampleFormControlInput1" className="form-label">Tags</label>
                <input type="text" className="form-control" id="txt_tags" value={postData.tags} onChange={(e)=>setPostData({...postData,tags:e.target.value.split(',')})}/>
            </div>
            <div className="mb-3 ">
            <FileBase64
                multiple={ false }
                onDone={({base64})=>setPostData({...postData,selectedFile:base64})}  />
            </div>
            <div className="m-3">
                <button type="button" className="btn btn-primary w-100" onClick={handleSubmit}>Submit</button>
            </div>
            <div className="m-3">
                <button type="button" className="btn btn-danger w-100"  onClick={handleClear}>Clear</button>
            </div>
        </div>
    </div>
  );
};
